@import url('https://fonts.googleapis.com/css2?family=Exo+2:wght@700&display=swap');

.app {
    font-family: 'Exo 2', sans-serif;
    width: 1000px;
    height: 1000px;
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
}

.header {
    width: 1000px;
    height: 100px;
    background: lightseagreen;
    display: flex;
    align-items: center;
}

.headerImgLogo {
    width: 80px;
    height: 80px;
    margin-left: 10px;
}

.nameLogo {
    margin: auto;
    font-size: 25px;
    color: white;
}